  <div class="deposit-page-step-1">
    <section class="deposit-page-step-1__credentials">
      <div class="deposit-page-step-1__credentials__qrcode">
        <p class="deposit-page-step-1__credentials-title">{{ t('depositPage.scan') }}</p>
        <div class="deposit-page-step-1__credentials__qrcode__body">
          <svg xmlns="http://www.w3.org/2000/svg" xmlns:xlink="http://www.w3.org/1999/xlink" width="144" height="142" viewBox="0 0 144 142" fill="none">
            <rect width="144" height="142" fill="url(#pattern0)"/>
            <defs>
              <pattern id="pattern0" patternContentUnits="objectBoundingBox" width="1" height="1">
                <use xlink:href="#image0" transform="translate(-0.125 -0.133803) scale(0.00563063)"/>
              </pattern>
              <image id="image0" width="222" height="222" xlink:href="data:image/png;base64,"/>
            </defs>
          </svg>
        </div>
      </div>
      <div class="deposit-page-step-1__credentials__divider">
        <Text
          variant="p"
          uppercase
          class="deposit-page-step-1__credentials__divider-text "
          :class="{'deposit-page-step-1__credentials__divider-text-ru': locale === 'ru'}"
        >{{ t('common.or') }}</Text>
      </div>
      <div class="deposit-page-step-1__credentials__manually">
        <p class="deposit-page-step-1__credentials-title">{{ t('depositPage.send') }}</p>
        <div class="deposit-page-step-1__credentials__manually__inputs">
          <Input
            class="deposit-page-step-1__credentials__manually__inputs-item"
            read-only
            :model-value="coinAmount"
          >
            <template #append>
              <Icon width="20px" height="20px" :name="sendCoin.key + '-circle'" />
            </template>
          </Input>
          <Input
            :model-value="code"
            class="deposit-page-step-1__credentials__manually__inputs-item"
            read-only
          >
            <template #append>
              <Icon
                class="deposit-page-step-1__credentials__manually__inputs-item__copy"
                width="14px"
                height="14px"
                name="copy"
              />
            </template>
          </Input>
        </div>
      </div>
    </section>
    <section class="deposit-page-step-1__payout-address">
      <h3>{{ t('depositPage.payoutAddress', { coin: sendCoin?.title || '' }) }}</h3>
      <p>3P3QsMVK89JBNqZQv5zMAKG8FK3kJM4rjt</p>
    </section>
    <Input v-model="email" :placeholder="emailPlaceholder" class="deposit-page-step-1__email">
      <template #prepend>
        <Icon class="deposit-page-step-1__email-notify-bell" name="notify-bell" />
      </template>
      <template #append>
        <Button class="deposit-page-step-1__email__button" type="primary" @click="processDeposit">
          <Icon name="arrow-right-full" />
        </Button>
      </template>
    </Input>
  </div>
</template>

<script>
import { MODULES_NAMES } from '@/common/modules'
import { useActions, useGetters } from '@/plugins/vuex'
import { useI18n } from 'vue-i18n'
import { computed, ref } from 'vue'
import Button from '@/components/UIKit/Button'
import Input from '@/components/UIKit/Input'
import Text from '@/components/Text/Text'
import Icon from '@/components/Icon/Icon'

export default {
  name: 'Step1',
  components: {
    Button,
    Input,
    Text,
    Icon
  },
  setup (_, { emit }) {
    const { t, locale } = useI18n()
    const coinAmount = ref(20.62151445)
    const code = ref('MVK89JBNqZQv5zMAKG8FK3kJM4rjt')
    const emailPlaceholder = ref('notifyme@myemail.com')

    const {
      getEmail,
      getSendCoin: sendCoin
    } = useGetters(MODULES_NAMES.EXCHANGE)
    const {
      setEmail
    } = useActions(MODULES_NAMES.EXCHANGE)

    const email = computed({
      get: () => getEmail.value,
      set: setEmail
    })
    const processDeposit = () => {
      emit('next', 1)
    }

    return {
      t,
      email,
      emailPlaceholder,
      locale,
      sendCoin,
      code,
      coinAmount,
      processDeposit
    }
  }
}
</script>

